.ba-slider {
	position: relative;
}

@media(max-width:991px) {
	.ba-slider {
		overflow: hidden;
	}
}

.ba-slider img {
	width: 100%;
	display: block;
	height: 100%;
}

.resize {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 50%;
	overflow: hidden;
}


.handle {
	/* Thin line seperator */
	position: absolute;
	left: 50%;
	top: 0;
	bottom: 0;
	width: 4px;
	margin-left: -2px;

	background: #fff;
	cursor: ew-resize;
}

.handle:after {
	/* Big orange knob  */
	position: absolute;
	top: 50%;
	width: 64px;
	height: 64px;
	margin: -30px 0 0 -30px;
	content: url('../img/svg/comfort-marker.svg');
	color: white;
	background: #fff;
	/* @orange */
	border: 1px solid #fff;
	/* darken(@orange, 5%) */
	border-radius: 50%;
	transition: all 0.3s ease;
	box-shadow:
		0 2px 6px rgba(0, 0, 0, .3),
		inset 0 2px 0 rgba(255, 255, 255, .5),
		inset 0 60px 50px -30px #fff;
	/* lighten(@orange, 20%)*/

	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-ms-align-items: center;
	align-items: center;
	justify-content: center;
}

.draggable:after {
	width: 48px;
	height: 48px;
	margin: -24px 0 0 -24px;
	line-height: 48px;
	font-size: 30px;
}